.container {
	max-width: 940px;
	margin: 0 auto;
	@include r(960) {
		margin: 0 10px; } }
.header {
	&__wrap {
		display: flex;
		text-align: left;
		padding: 22px 0;
		align-items: baseline; }
	&__logo {
		text-transform: uppercase; }
	&__nav {
		text-transform: uppercase;
		margin-left: auto; } }
.logo {
	font-size: 36px;
	color: #e8603c;
	@include bebas;
	&__Ro {
		color: #f3af9d; } }

.navigation {
	font-size: 16px;
	position: relative;
	top: -6px;
	@include bebas;
	a {
		color: #b9b9b9;
		margin-left: 24px;
		&.is-active,&:hover {
			color: #e8663c; } } }

.blog {
	overflow: hidden;
	margin-bottom: 24px;
	&__heading {
		position: relative;
		font-size: 24px;
		line-height: 1.3;
		color: #fff;
		padding: 15px 0 15px 18px;
		text-transform: uppercase;
		background: #e8603c;
		@include bebas;
		&:before,
		&:after {
			position: absolute;
			height: 61px;
			background: #f2ae9c;
			content: "";
			width: 100%;
			top: 0; }
		&:before {
			right: 100%;
			margin-right: 10px; }
		&:after {
			left: 100%;
			margin-left: 10px; } } }
.main-blog {
	display: flex; }
.main {
	&__wrap,&__wrapp-middle {
		position: relative;
		&:before {
			position: absolute;
			content: "";
			top: 0;
			height: 75px;
			width: 75px;
			background: url("../icons/top-icon.png") no-repeat;
			background-size: cover;
			z-index: 100;
			right: 100%;
			margin-right: 10px; } }

	&__wrapp-middle {
		&:before {
			background: url("../icons/middle-icon.png") no-repeat; } } }




.section {
	max-width: 670px;
	margin-bottom: 20px; }


.section-middle {}


.slider {
	position: relative;
	&__next,
	&__prev {
		position: absolute;
		top: 50%;
		left: 20px;
		cursor: pointer;
		z-index: 50;
		font-size: 54px;
		margin-top: -25px; }
	&__next {
		left: auto;
		right: 20px;
		transform: rotate(-180deg); }
	&__slide {
		position: relative;
		height: 0;
		padding-bottom: 44.8%; } }


.inner {
	position: relative;
	max-width: 650px;
	width: 100%;
	display: flex;
	margin-bottom: 70px;
	&__info {
		margin-bottom: 15px; }
	&__wrap {
		min-width: 23%; }
	&__link {
		display: block; }
	&__description {
		font-size: 12px;
		color: #bababa;
		white-space: nowrap; }
	&__heading {
		font-size: 12px;
		color: #e8603c;
		text-transform: uppercase;
		line-height: 1.6;
		margin-bottom: 5px;
		@include bebas; }
	&__title {
		@include bebas;
		font-size: 18px;
		line-height: 1;
		color: #e8603c;
		text-transform: uppercase;
		margin-bottom: 20px;
		&+p {
			margin-bottom: 25px; } }
	&__dot {
		color: #8caf2f; }
	&:before {
		position: absolute;
		content: "";
		background: url('../img/pattern.png') 0 0;
		bottom: -40px;
		height: 14px;
		width: 100%; } }
.inner__promo {
		p {
			font-family: Helvetica, sans-serif;
			font-size: 12px;
			line-height: 1.5;
			color: #414141;
			font-style: normal; } }

