html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

nav,
ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

a,
span,
*:before,
*:after {
  display: inline-flex;
  text-decoration: none;
  color: inherit;
}

.icon-arrow {
  width: 0.69em;
  height: 1em;
  fill: #FFF;
}

.icon-doc {
  width: 1em;
  height: 1em;
  fill: #E8603C;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

@font-face {
  font-family: "bebas";
  src: url("../fonts/bebas-webfont.woff2") format("woff2"), url("../fonts/bebas-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: Helvetica,sans-serif;
}

.container {
  max-width: 940px;
  margin: 0 auto;
}

@media only screen and (max-width: 960px) {
  .container {
    margin: 0 10px;
  }
}

.header__wrap {
  display: flex;
  text-align: left;
  padding: 22px 0;
  align-items: baseline;
}

.header__logo {
  text-transform: uppercase;
}

.header__nav {
  text-transform: uppercase;
  margin-left: auto;
}

.logo {
  font-size: 36px;
  color: #e8603c;
  font-family: bebas,sans-serif;
}

.logo__Ro {
  color: #f3af9d;
}

.navigation {
  font-size: 16px;
  position: relative;
  top: -6px;
  font-family: bebas,sans-serif;
}

.navigation a {
  color: #b9b9b9;
  margin-left: 24px;
}

.navigation a.is-active, .navigation a:hover {
  color: #e8663c;
}

.blog {
  overflow: hidden;
  margin-bottom: 24px;
}

.blog__heading {
  position: relative;
  font-size: 24px;
  line-height: 1.3;
  color: #fff;
  padding: 15px 0 15px 18px;
  text-transform: uppercase;
  background: #e8603c;
  font-family: bebas,sans-serif;
}

.blog__heading:before, .blog__heading:after {
  position: absolute;
  height: 61px;
  background: #f2ae9c;
  content: "";
  width: 100%;
  top: 0;
}

.blog__heading:before {
  right: 100%;
  margin-right: 10px;
}

.blog__heading:after {
  left: 100%;
  margin-left: 10px;
}

.main-blog {
  display: flex;
}

.main__wrap, .main__wrapp-middle {
  position: relative;
}

.main__wrap:before, .main__wrapp-middle:before {
  position: absolute;
  content: "";
  top: 0;
  height: 75px;
  width: 75px;
  background: url("../icons/top-icon.png") no-repeat;
  background-size: cover;
  z-index: 100;
  right: 100%;
  margin-right: 10px;
}

.main__wrapp-middle:before {
  background: url("../icons/middle-icon.png") no-repeat;
}

.section {
  max-width: 670px;
  margin-bottom: 20px;
}

.slider {
  position: relative;
}

.slider__next, .slider__prev {
  position: absolute;
  top: 50%;
  left: 20px;
  cursor: pointer;
  z-index: 50;
  font-size: 54px;
  margin-top: -25px;
}

.slider__next {
  left: auto;
  right: 20px;
  transform: rotate(-180deg);
}

.slider__slide {
  position: relative;
  height: 0;
  padding-bottom: 44.8%;
}

.inner {
  position: relative;
  max-width: 650px;
  width: 100%;
  display: flex;
  margin-bottom: 70px;
}

.inner__info {
  margin-bottom: 15px;
}

.inner__wrap {
  min-width: 23%;
}

.inner__link {
  display: block;
}

.inner__description {
  font-size: 12px;
  color: #bababa;
  white-space: nowrap;
}

.inner__heading {
  font-size: 12px;
  color: #e8603c;
  text-transform: uppercase;
  line-height: 1.6;
  margin-bottom: 5px;
  font-family: bebas,sans-serif;
}

.inner__title {
  font-family: bebas,sans-serif;
  font-size: 18px;
  line-height: 1;
  color: #e8603c;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.inner__title + p {
  margin-bottom: 25px;
}

.inner__dot {
  color: #8caf2f;
}

.inner:before {
  position: absolute;
  content: "";
  background: url("../img/pattern.png") 0 0;
  bottom: -40px;
  height: 14px;
  width: 100%;
}

.inner__promo p {
  font-family: Helvetica, sans-serif;
  font-size: 12px;
  line-height: 1.5;
  color: #414141;
  font-style: normal;
}
